@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}
.coinContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 30px;
  color: #fff;
}
.header{
  display: flex;
  justify-content: space-around;
  margin-top: 40px;
}
.inputField{
  padding-left: 16px;
  font-size: 14px;
  width:260px;
  height: 42px;
  border-radius: 4px;
  border: none;
  box-shadow: 0 0 3pt 2pt white ;
  color: #e2e2e2;
}
@media (max-width: 520px) {
  .header{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  h1.brand{
    padding-bottom: 16px;
  }
}

.cryptoCoin{
    overflow: hidden;
    text-align: center;
    border-radius: 1rem;
    position: relative;
    width: 320px;
    margin: 2rem;
    box-sizing: 0 3px 3px rgba(0, 0, 0, 0.5);
}
.coinLogo{
    width: 8rem;
    margin-top: 1.2rem;
    height: 40px;
    width: 40px;
    -webkit-clip-path: circle(60px at center);
            clip-path: circle(60px at center);
}
.coinNameWrap{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px;
    font-weight: 700;
}
.coinName{
    font-weight: bold;
    font-size: 1.5rem;
}
.coinSymbol{
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;
    padding-left: 80px;
}
.priceContainerUP{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    border: none;
    font-size: 1rem;
    color: white;
    padding: 1rem;
    background-color: #11d811;
}
.priceContainerDOWN{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    border: none;
    font-size: 1rem;
    color: white;
    padding: 1rem;
    background-color: #F00606;
}
svg{
    background-color: transparent;
}
.priceChange{
    background-color: transparent;
    padding-left: 10px;
    text-shadow: 0 0 14px black;
}
.coinPrice{
    width: auto;
    background-color: transparent;
}
.coinMarketcap{
    width: auto;
    padding-top: 8px;
    background-color: transparent;
}
.coinVolume{
    width: auto;
    padding-top: 6px;
    padding-bottom: 8px;
    background-color: transparent;
}
